.overlay1 {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1000;
}
.modal1 {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: black;
  padding: 50px;
  z-index: 1000;
  border-radius: 20px;

  .button {
    cursor: pointer;
    height: 60px;
    font-size: 50px;
    background-color: gray;
    overflow: hidden;
    border-radius: 10px;
  }
}
